import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Button, Card, CardBody, CardDeck, Col, Modal, ModalBody, ModalFooter, ModalHeader, Row, Table, UncontrolledTooltip } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertType,
  Box,
  findAllByExpression,
  InputGroupInline,
  OperationEnum,
  ProcessBar,
  SearchCriteria,
  SearchExpression,
  showToast,
  SUCCESS,
  Tree,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { CLASS_NAME, ContaResultado } from '../../../../models/dtos/mcg/contaResultado';
import { ContaResultadoClassificacao } from '../../../../models/dtos/mcg/contaResultadoClassificacao';
import {
  CUSTO_DE_MERCADORIA_VENDIDA,
  CUSTO_PROD_VEND_SERV_PRESTADO,
  DEDUCAO_RECEITA_BRUTA,
  DESPESA_FINANCEIRA,
  DESPESA_NAO_OPERACIONAL,
  DESPESA_NAO_TRIBUTAVEL,
  DESPESA_OPERACIONAL,
  DISTRIBUICAO_DE_RESERVA,
  OUTRA_RECEITA_OPERACIONAL,
  PROVISAO_CSLL,
  PROVISAO_IRPJ,
  RECEITA_FINANCEIRA,
  RECEITA_NAO_OPERACIONAL,
  RECEITA_NAO_TRIBUTAVEL,
  RECEITA_OPERACIONAL_BRUTA,
  RESULTADO_DO_IMPOSTO_DE_RENDA,
} from '../../../../models/enumerated/mcg/contaResultadoMCGEnum';
import { excluirContaResultado, prepararContaResultado, salvarContaResultado } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import PlanoContaIS from '../../../components/mcg/planoContaIS/PlanoContaIS';

export interface FiltroContaResultado {
  conta?: string;
  caracteres?: string;
}

const ContaResultadoMCGTreeView: FC<ContaResultado> = () => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const [listaContaResultado, setListaContaResultado] = useState<ContaResultado[]>([]);
  const [listaContaResultadoCompleta, setListaContaResultadoCompleta] = useState<ContaResultado[]>([]);
  const [showModalView, setShowModalView] = useState(false);
  const [showModalViewPlanoConta, setShowModalViewPlanoConta] = useState(false);
  const { reset, ...formProps } = useForm<ContaResultado>({ mode: 'onBlur' });
  const formPropsConta = useForm<ContaResultadoClassificacao>({ mode: 'onBlur' });
  const [ehRenderizaTree, setEhRenderizaTree] = useState();
  const [tabela, setTabela] = useState();
  const [contaResultadoAtual, setContaResultadoAtual] = useState<ContaResultado>();
  const [atualizado, setAtualizado] = useState();
  const [idAtual, setIdAtual] = useState();
  const [idPlanoConta, setIdPlanoConta] = useState();
  const [statusPlanoContaIS, setStatusPlanoContaIS] = useState();
  const [statusTela, setStatusTela] = useState();
  const { digitoAtivo, digitoPassivo } = globalParameter.grupoPlanoConta;

  useEffect(
    () => {
      prepararContaResultado(globalParameter.grupoPlanoConta.id, {
        thenFunction: () => {
          montarTree();
        },
      });
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const montarTree = () => {
    const searchCriteria: SearchCriteria = {
      className: CLASS_NAME,
      expressions: [
        {
          fieldName: 'grupoPlanoConta.id',
          value: globalParameter.grupoPlanoConta.id,
        },
      ],
      fetchType: 'PAGED',
      max: 0,
      sortFieldName: 'tipoContaResultado',
      start: 0,
    };

    findAllByExpression(searchCriteria, {
      thenFunction: result => {
        const lista: ContaResultado[] = result.content;
        const listaParaRendereizar: ContaResultado[] = [];

        lista.forEach(item => {
          if (item.contaResultadoPai) {
            lista.forEach(item2 => {
              if (item.contaResultadoPai.id === item2.id) {
                item2.children.push(item);
              }
            });
          } else {
            listaParaRendereizar.push(item);
          }
        });
        setListaContaResultado(listaParaRendereizar);
        setListaContaResultadoCompleta(lista);
        setEhRenderizaTree(!ehRenderizaTree);
      },
    });
  };

  const handleLabelClick = () => {
    return (e, id) => {
      e.preventDefault();
      setIdAtual(id);
    };
  };

  const incluindoContaResultado = contaResultadoFormProps => {
    const novaContaResultado: ContaResultado = {
      contaResultadoPai: contaResultadoFormProps,
      contas: [],
      grupoPlanoConta: { id: globalParameter.grupoPlanoConta.id },
      tipoContaResultado: contaResultadoFormProps.tipoContaResultado,
    };
    reset(novaContaResultado);
    setContaResultadoAtual(novaContaResultado);
  };

  const alterandoContaResultado = contaResultadoFormProps => {
    reset(contaResultadoFormProps);
    setContaResultadoAtual(contaResultadoFormProps);
  };

  const excluindoContaResultado = () => {
    if (listaContaResultado.find(element => element.id === idAtual)) {
      mostrarErro('Exclusão não permitida para o registro selecionado');
    } else {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'Confirmar exclusão?',
        onOkClick: () => {
          excluirContaResultado(
            listaContaResultadoCompleta.find(element => element.id === idAtual),
            {
              errorFunction: () => {
                mostrarErro(
                  <div>
                    Não foi possível realizar estar operação. Possíveis causas:
                    <br />
                    <br />
                    *O registro ainda é referenciado nas Contas de Resultado
                    <br />
                    *Outro usuário pode ter excluído o registro
                  </div>
                );
              },
              thenFunction: () => {
                montarTree();
              },
            }
          );
        },
        type: AlertType.WARNING_YES_NO,
      });
    }
  };

  const handleAcoes = (status: string, showModal: boolean) => {
    return () => {
      if (idAtual) {
        setStatusTela(status);
        const contaResultadoFormProps = listaContaResultadoCompleta.find(element => element.id === idAtual);
        if (status === 'incluindo') {
          incluindoContaResultado(contaResultadoFormProps);
        } else if (status === 'alterando') {
          alterandoContaResultado(contaResultadoFormProps);
        } else if (status === 'excluindo') {
          excluindoContaResultado();
        }
        setShowModalView(showModal);
      } else {
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: 'Selecione um Registro',
          type: AlertType.INFORMATION,
        });
      }
    };
  };

  useEffect(
    () => {
      setTabela(
        contaResultadoAtual ? (
          contaResultadoAtual.contas.map((item, index) => {
            return (
              <tr key={index}>
                <td>{item.planoConta.conta}</td>
                <td>{item.planoConta.descricao}</td>
                <td>
                  <Button id="editButton" size="sm" color="link" className="text-warning" onClick={handleOpenModalPlanoConta(item.planoConta.id)}>
                    <FontAwesomeIcon icon="pen" />
                  </Button>
                  <UncontrolledTooltip target="editButton">Clique aqui para alterar</UncontrolledTooltip>
                  <Button id="removeButton" size="sm" color="link" className="text-danger" onClick={removerDaLista(item.planoConta.id)}>
                    <FontAwesomeIcon icon="trash" />
                  </Button>
                  <UncontrolledTooltip target="removeButton">Clique aqui para remover</UncontrolledTooltip>
                </td>
              </tr>
            );
          })
        ) : (
          <tr>
            <td />
            <td />
            <td />
          </tr>
        )
      );
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [contaResultadoAtual, atualizado]
  );

  const addNaLista = () => {
    if (formPropsConta.getValues().planoConta) {
      if (
        listaContaResultadoCompleta.filter(contaResultado =>
          contaResultado.contas.find(conta => conta.planoConta.id === formPropsConta.getValues().planoConta.id)
        ).length > 0
      ) {
        mostrarErro('Conta já informada');
      } else if (
        formPropsConta.getValues().planoConta.conta.charAt(0) === digitoAtivo ||
        formPropsConta.getValues().planoConta.conta.charAt(0) === digitoPassivo
      ) {
        mostrarErro('A conta informada não deve pertencer a Ativo ou Passivo');
      } else {
        if (statusPlanoContaIS === 'alterando') {
          contaResultadoAtual.contas = contaResultadoAtual.contas.filter(element => element.planoConta.id !== idPlanoConta);
        }
        const contaResultadoClassificacao: ContaResultadoClassificacao = {
          planoConta: formPropsConta.getValues().planoConta,
        };
        contaResultadoAtual.contas.push(contaResultadoClassificacao);
        setAtualizado(!atualizado);
        setShowModalViewPlanoConta(false);
        formPropsConta.setValue('planoConta', undefined);
      }
    } else {
      mostrarErro('Conta não pode estar em branco');
    }
  };

  const removerDaLista = id => {
    return () => {
      contaResultadoAtual.contas = contaResultadoAtual.contas.filter(element => element.planoConta.id !== id);
      setAtualizado(!atualizado);
    };
  };

  const handleSubmit = () => {
    // regex verifica se o campo descricao está vazio ou se contém somente espaços em branco
    if (formProps.getValues().descricao.replace(/\s/g, '').length) {
      contaResultadoAtual.descricao = formProps.getValues().descricao;
      if (
        (statusTela === 'alterando' &&
          listaContaResultadoCompleta.find(
            element => element.descricao === contaResultadoAtual.descricao && element.id !== contaResultadoAtual.id
          )) ||
        (statusTela === 'incluindo' && listaContaResultadoCompleta.find(element => element.descricao === formProps.getValues().descricao))
      ) {
        mostrarErro('Já existe um Grupo com este nome');
      } else {
        salvarContaResultado(contaResultadoAtual, {
          thenFunction: () => {
            showToast('Registro alterado com sucesso!', SUCCESS);
            setShowModalView(false);
            montarTree();
          },
        });
      }
    } else {
      formProps.triggerValidation('descricao');
    }
  };

  const handleCloseModal = () => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message: 'As alterações da página atual serão perdidas, deseja continuar?',
      onOkClick: () => {
        setShowModalView(false);
        formPropsConta.setValue('planoConta', undefined);
        montarTree();
      },
      type: AlertType.WARNING_YES_NO,
    });
  };

  const handleOpenModalPlanoConta = idConta => {
    return () => {
      setIdPlanoConta(idConta);
      if (idConta !== '') {
        setStatusPlanoContaIS('alterando');
        formPropsConta.reset(contaResultadoAtual.contas.find(element => element.planoConta.id === idConta));
      }
      setShowModalViewPlanoConta(true);
    };
  };

  const handleCloseModalPlanoConta = () => {
    setShowModalViewPlanoConta(false);
    formPropsConta.setValue('planoConta', undefined);
  };

  const mostrarErro = mensagemErro => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message: mensagemErro,
      type: AlertType.ERROR,
    });
  };

  const restaurarDescOriginal = () => {
    let descricao;

    switch (contaResultadoAtual.tipoContaResultado) {
      case RECEITA_OPERACIONAL_BRUTA.value:
        descricao = RECEITA_OPERACIONAL_BRUTA.label;
        break;

      case DEDUCAO_RECEITA_BRUTA.value:
        descricao = DEDUCAO_RECEITA_BRUTA.label;
        break;

      case CUSTO_PROD_VEND_SERV_PRESTADO.value:
        descricao = CUSTO_PROD_VEND_SERV_PRESTADO.label;
        break;

      case CUSTO_DE_MERCADORIA_VENDIDA.value:
        descricao = CUSTO_DE_MERCADORIA_VENDIDA.label;
        break;

      case DESPESA_OPERACIONAL.value:
        descricao = DESPESA_OPERACIONAL.label;
        break;

      case DESPESA_FINANCEIRA.value:
        descricao = DESPESA_FINANCEIRA.label;
        break;

      case RECEITA_FINANCEIRA.value:
        descricao = RECEITA_FINANCEIRA.label;
        break;

      case OUTRA_RECEITA_OPERACIONAL.value:
        descricao = OUTRA_RECEITA_OPERACIONAL.label;
        break;

      case RECEITA_NAO_OPERACIONAL.value:
        descricao = RECEITA_NAO_OPERACIONAL.label;
        break;

      case DESPESA_NAO_OPERACIONAL.value:
        descricao = DESPESA_NAO_OPERACIONAL.label;
        break;

      case RECEITA_NAO_TRIBUTAVEL.value:
        descricao = RECEITA_NAO_TRIBUTAVEL.label;
        break;

      case DESPESA_NAO_TRIBUTAVEL.value:
        descricao = DESPESA_NAO_TRIBUTAVEL.label;
        break;

      case RESULTADO_DO_IMPOSTO_DE_RENDA.value:
        descricao = RESULTADO_DO_IMPOSTO_DE_RENDA.label;
        break;

      case PROVISAO_CSLL.value:
        descricao = PROVISAO_CSLL.label;
        break;

      case PROVISAO_IRPJ.value:
        descricao = PROVISAO_IRPJ.label;
        break;

      case DISTRIBUICAO_DE_RESERVA.value:
        descricao = DISTRIBUICAO_DE_RESERVA.label;
        break;
    }
    formProps.setValue('descricao', descricao);
  };

  const retornoLabelFunc = () => {
    return (node: any) => {
      return <div className={node.contaResultadoPai ? null : 'font-weight-bold'}>{node.descricao}</div>;
    };
  };

  const getFiltro = () => {
    const valores: FiltroContaResultado[] = [];

    listaContaResultadoCompleta.forEach(contaResultado => {
      contaResultado.contas.forEach(it => {
        valores.push({ conta: it.planoConta.conta });
        let aux = '';
        for (const caracter of it.planoConta.conta) {
          aux += caracter;
          valores.push({ caracteres: aux });
        }
      });
    });

    return valores;
  };

  const getExtraExpressions = (indice: number) => {
    const extraExpressions: SearchExpression[] = [];

    const valores = getFiltro();

    if (valores[indice]) {
      extraExpressions.push({
        alias: 'c' + indice,
        conditionalGroup: valores[indice].conta ? 'c' : 'c_',
        fieldName: 'conta',
        operation: valores[indice].conta ? OperationEnum.START_NOT_LIKE : OperationEnum.DIFERENT,
        value: valores[indice].conta || valores[indice].caracteres,
      });
      extraExpressions[0].expressions = getExtraExpressions(indice + 1);
    }

    return extraExpressions;
  };

  return (
    <WindowComponent
      title="Manutenção de Contas de Resultado"
      wikiId={1215}
      useGlobalParameter={true}
      globalParameter={globalParameter}
      dependencies={[() => hasEmpresa(globalParameter), () => hasGrupoEmpresaConfig(globalParameter)]}
    >
      <Row>
        <Col>
          <Button className="mb-2 mr-2" color="success" onClick={handleAcoes('incluindo', true)}>
            Incluir
          </Button>
          <Button className="mb-2 mr-2" color="first" onClick={handleAcoes('alterando', true)}>
            Alterar
          </Button>
          <Button className="mb-2 mr-2" color="danger" onClick={handleAcoes('excluindo', false)}>
            Excluir
          </Button>
        </Col>
      </Row>
      <Box style={{ overflowY: 'scroll', height: 450 }}>
        <Tree
          parentNodesIdList={listaContaResultado.map((it, index) => index)}
          listenerRenderTree={ehRenderizaTree}
          nodeList={listaContaResultado}
          labelFunc={retornoLabelFunc()}
          labelClickFunc={handleLabelClick()}
        />
      </Box>
      <Modal isOpen={showModalView} backdrop="static" centered={true} toggle={handleCloseModal} size="lg">
        <ModalHeader toggle={handleCloseModal}>Conta Resultado</ModalHeader>
        <CardDeck>
          <Card>
            <CardBody>
              <InputGroupInline id="id" formProps={formProps} hidden={true} />
              <InputGroupInline id="grupoPlanoConta.id" formProps={formProps} hidden={true} />
              <InputGroupInline id="tipoContaResultado" formProps={formProps} hidden={true} />
              <Row>
                <Col>
                  <InputGroupInline id="contaResultadoPai.descricao" formProps={formProps} isPrependLabel={true} label="Grupo" disabled={true} />
                </Col>
              </Row>
              <Row>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true } }}
                    id="descricao"
                    isPrependLabel={true}
                    label="Descrição"
                    placeholder="Informe a Descrição"
                  />
                </Col>
                {contaResultadoAtual && contaResultadoAtual.contaResultadoPai === null ? (
                  <Col className="col-2">
                    <Button id="resetDesc" size="lg" color="link" className="text-success" onClick={restaurarDescOriginal}>
                      <FontAwesomeIcon icon="reply" />
                    </Button>
                    <UncontrolledTooltip target="resetDesc">Restaurar nome original</UncontrolledTooltip>
                  </Col>
                ) : null}
              </Row>
              <Row>
                <Col>
                  <Table style={{ textAlign: 'center' }} hover={true} striped={true} bordered={true}>
                    <thead>
                      <tr>
                        <th style={{ width: '30%' }}>Conta</th>
                        <th style={{ width: '40%' }}>Descrição</th>
                        <th style={{ width: '30%' }}>Ações</th>
                      </tr>
                    </thead>
                    <tbody>{tabela}</tbody>
                  </Table>
                </Col>
                <Col className="col-2">
                  <Button id="addButton" size="lg" color="link" className="text-success" onClick={handleOpenModalPlanoConta('')}>
                    <FontAwesomeIcon icon="plus-circle" />
                  </Button>
                  <UncontrolledTooltip target="addButton">Clique aqui para adicionar</UncontrolledTooltip>
                </Col>
              </Row>
              <ModalBody />
              <ModalFooter>
                <Row>
                  <Col>
                    <ProcessBar handleSubmit={handleSubmit} handleClose={handleCloseModal} />
                  </Col>
                </Row>
              </ModalFooter>
            </CardBody>
          </Card>
        </CardDeck>
        <Modal isOpen={showModalViewPlanoConta} backdrop="static" centered={true} toggle={handleCloseModalPlanoConta} size="lg">
          <ModalHeader toggle={handleCloseModalPlanoConta}>Conta Resultado</ModalHeader>
          <CardDeck>
            <Card>
              <CardBody>
                <InputGroupInline id="descricao" formProps={formProps} isPrependLabel={true} label="Grupo" disabled={true} />
                <Row>
                  <Col>
                    <PlanoContaIS
                      id="planoConta"
                      formProps={{ ...formPropsConta, validation: { required: true } }}
                      label="Conta"
                      placeholder="Informe a Conta"
                      filtroContaResultado={getExtraExpressions(0)}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <ProcessBar handleSubmit={addNaLista} handleClose={handleCloseModalPlanoConta} />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </CardDeck>
        </Modal>
      </Modal>
    </WindowComponent>
  );
};

export default ContaResultadoMCGTreeView;
